.floatingCta {
  width: 97.5%;
  display: flex;
  justify-content: center; /* Center the items horizontally */
  position: fixed;
  bottom: 0;
  margin-bottom: 50px;
  padding: 10px;
}
.floatingCta .container {
  display: flex;
  justify-content: space-between; /* Space items evenly between the start and end */
  align-items: center; /* Center items vertically */
  width: 100%;
  max-width: 800px; /* Set a maximum width for the container */
  background-color: #ffffff; /* Background color for the container */
  border-radius: 10px; /* Add rounded corners to the container */
  padding: 10px 10px 10px 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Add a shadow to the container */
}
.floatingCta .container .destination,
.floatingCta .container .days,
.floatingCta .container .price {
  flex: 1; /* Distribute available space evenly among the items */
  padding: 5px;
}
.floatingCta .container button a {
  text-decoration: none !important;
}
.floatingCta .container button:hover a {
  color: white;
}